.tile{width:50px;height:50px;display:inline-block;vertical-align:top}.stone{background-image:url(images/stonewall.png);background-size:contain;border:2px solid #B9B799}.sand{background-image:url(images/sandwall.png);background-size:contain;border:2px solid orange}.key{background-image:url(images/key.png);background-size:contain}.door{background-image:url(images/door.png);background-size:cover;border:2px solid orange;background-color:#e4e4e4}.coin{background-color:yellow;border-radius:50%;transform:scale(0.25)}.pacman{background-image:url(images/pacman.png);background-size:contain}.left{background-image:url(images/pacman-left.png)}.up{background-image:url(images/pacman-up.png)}.down{background-image:url(images/pacman-down.png)}.mummy{background-image:url(images/mummy.png);background-size:contain}#score{color:red}#level{color:green}#music{color:yellow}#game-info{display:flex;flex-direction:row;justify-content:space-between;width:650px;margin:auto;margin-top:50px;font-size:20px;font-weight:bold}#game-controls{display:flex;flex-direction:row;justify-content:space-between;width:670px;margin:auto;font-size:20px;font-weight:bold}#body{margin-top:10px;background-color:black}#body div{z-index:3}#background-images{display:flex;flex-direction:row;justify-content:space-around;align-items:flex-end;margin-left:30px}.image1{width:280px;height:310px;background-image:url(images/pacman-background.png);background-size:contain;margin-bottom:-388px}.image2{width:300px;height:300px;background-image:url(images/mummy.png);background-size:contain;margin-bottom:-384px}#game-over{margin-top:115px;font-size:60px;font-weight:bold;color:white;height:auto}.restart{font-size:20px;font-weight:bold;color:whitesmoke;margin:10px}#background-music{font-size:20px;font-weight:bold;color:yellow;margin:10px}
